import React, { useState } from "react";
import css from "classnames";

const ConfessionParagraph = ({ paragraph, index }) => {
  const [activeScripture, setActiveScripture] = useState(null);

  return (
    <div>
      <h4>Chapter {index + 1}</h4>
      <p>
        {paragraph.map(section => {
          return (
            <span key={section.superscript}>
              {section.text}
              <a
                href={`#ref-${index}-${section.superscript}`}
                id={`${index}-${section.superscript}`}
                className="superscript"
              >{`${section.superscript} `}</a>
            </span>
          );
        })}
      </p>

      <div className="scripture-section">
        <p className="nomargin">
          <strong>References (click to view)</strong>
        </p>
        {paragraph.map(section => {
          return (
            <p key={section.superscript} className="nomargin">
              <a
                href={`#${index}-${section.superscript}`}
                id={`ref-${index}-${section.superscript}`}
              >
                {`^ `}
              </a>
              <strong>{section.superscript}.</strong>{" "}
              {section.scriptures.map((scripture, index) => {
                const classNames = css("scripture", {
                  "scripture--active": scripture === activeScripture
                });

                return (
                  <span
                    key={scripture}
                    className={classNames}
                    onClick={() => setActiveScripture()}
                  >
                    {scripture}
                    {index === section.scriptures.length - 1 ? "" : `, `}
                  </span>
                );
              })}
            </p>
          );
        })}

        <p className="first">
          <em>Click on a Scripture above to view passage</em>
        </p>
      </div>

      <style jsx>{`
        p {
          font-size: 21px;
          line-height: 1.58;
          margin: 0;
          margin-top: 29px;
        }

        .first {
          margin-top: 8px;
        }

        .nomargin {
          margin-top: 0;
        }

        a {
          color: #689f38;
          text-decoration: none;
        }

        @media screen and (max-width: 768px) {
          p {
            font-size: 18px;
            margin-top: 21px;
          }
        }

        .superscript {
          color: #689f38;
          font-size: 15px;
          margin-left: 1px;
          position: relative;
          top: -6px;
        }

        .scripture-section {
          border: 1px solid #bdbdbd;
          margin-top: 20px;
          padding: 5px 10px;
        }

        .scripture {
          color: rgba(0, 0, 0, 0.54);
          cursor: pointer;
          margin-right: 5px;
        }

        .scripture--active {
          color: rgba(0, 0, 0, 0.84);
        }
      `}</style>
    </div>
  );
};

export default ConfessionParagraph;
